
*{
  margin: 0;
  padding:0;
}
html{
  font-size:100px;
}
li{
  list-style:none;
}
a{
  text-decoration: none;
}
footer{
  width:100%;
  height:1.4rem;
  background:rgb(244,245,246);
  position:fixed;
  bottom:0;
}
.footer-icon:after{
  clear:both;
  display:block;
  content:'';
}
.footer-icon{
  height:1.4rem;
}
.iconn{
  float:left;
  width:25%;
  text-align: center;
  font-size:0.3rem;
  margin-top:0.2rem;
}
.iconn p{
  color:black;
}
.iconn .color{
  color:rgb(248,89,89);
}
.svv{
  width:0.64rem;
  height:0.64rem;
 /* color:green;*/
}



